export const customStyles = {
  control: (base, state) => ({
    ...base,
    borderColor: state.isFocused ? "#383332" : "##383332",
    boxShadow: state.isFocused ? null : null,
    "&:hover": { borderColor: state.isFocused ? null : null },
    minHeight: 28,
    height: 28,
    width: "14rem",
    padding: "0px 0px",
  }),
  option: (base, state) => ({
    ...base,
    backgroundColor: state.isSelected ? "#FFC467" : "##F8F4F0",
    color: state.isSelected ? "#383332" : "#383332",
    "&:hover": { backgroundColor: "#F8F4F0" },
    minHeight: 18,
    width: 250,
    paddingBottom: 4,
  }),
  dropdownIndicator: (base) => ({
    ...base,
    padding: 4,
  }),
  input: (base, state) => ({
    ...base,
    padding: "0px 0px 0px",
  }),
  loadingIndicator: (base) => ({
    ...base,
    display: "none",
  }),
  menuList: (base) => ({
    ...base,
    maxHeight: 200,

    "::-webkit-scrollbar": {
      width: "4px",
      height: "0px",
    },
    "::-webkit-scrollbar-track": {
      background: "#f1f1f1",
    },
    "::-webkit-scrollbar-thumb": {
      background: "#888",
    },
    "::-webkit-scrollbar-thumb:hover": {
      background: "#555",
    },
  }),
};

export const logout = () => {
  localStorage.removeItem("beaconToken");
  localStorage.removeItem("tenantToken");
  localStorage.removeItem("projectExchangeToken");
};

export const showErrorMessage = (id, message) => {
  document.getElementById(id).style.display = "block";
  document.getElementById(id).innerHTML = message;
  setTimeout(() => {
    document.getElementById(id).style.display = "none";
    document.getElementById(id).innerHTML = "";
  }, 3000);
};
export const successMessage = (id, message) => {
  document.getElementById(id).style.display = "block";
  document.getElementById(id).innerHTML = message;
  setTimeout(() => {
    document.getElementById(id).style.display = "none";
    document.getElementById(id).innerHTML = "";
  }, 3000);
};
export const checkAlreadyReserved = async (mappingConfig, selectedAddress) => {
  return new Promise((resolve, reject) => {
    if (mappingConfig) {
      Object.entries(mappingConfig).reduce((check, element) => {
        if (element[1]?.address === selectedAddress) resolve(true);
      });
      resolve(false);
    } else {
      resolve(false);
    }
  });
};
