export const getLastUsedRowNumber = async () => {
  return await Excel.run(async function (context) {
    const sheet = context.workbook.worksheets.getActiveWorksheet();
    const lastRow = sheet.getUsedRange().load("rowCount");
    await context.sync();
    console.log(lastRow.rowCount);
    return lastRow.rowCount;
  });
};
// this function creates new sheet and set it active sheet
//  if sheet already exists then it automatically provides new name by adding numbers like "sheetname-1"
export const createNewSheet = async (sheetname, notActivate = false) => {
  if (sheetname.length >= 25) sheetname = sheetname.substring(0, 27);
  let sheetNames = await getAllsheetNames();
  let condition = true,
    i = 1;
  while (condition) {
    if (sheetNames?.includes(sheetname)) {
      sheetname = i === 1 ? sheetname + `-${i}` : sheetname.slice(0, -((Math.log(i) * Math.LOG10E + 2) | 0)) + `-${i}`;
      i++;
    } else {
      condition = false;
      break;
    }
  }
  return await Excel.run(async function (context) {
    const workbook = context.workbook;
    const newSheet = workbook.worksheets.add(String(sheetname));
    // if there is Notactivate arguement then the sheet wont't activated
    notActivate && newSheet.activate();
    context.load(newSheet, "name");
    context.load(workbook, "name");
    return context
      .sync()
      .then(function () {
        return sheetname;
      })
      .catch(function (error) {
        console.log("Error: " + error);
        if (error instanceof OfficeExtension.Error) {
          console.log("Debug info: " + JSON.stringify(error.debugInfo));
        }
      });
  });
}; // need to handle if a sheet alraedy exists

// getall active sheet in the given selecetd workbook names and return it as an array
export const getAllsheetNames = async (sheetnamesForDD) => {
  return await Excel.run(function (context) {
    const worksheets = context.workbook.worksheets;
    context.load(worksheets, "items/name");
    return context
      .sync()
      .then(function () {
        let sheetNames = [];
        if (sheetnamesForDD) {
          for (let i = 0; i < worksheets.items.length; i++) {
            sheetNames.push({ label: worksheets.items[i].name, value: worksheets.items[i] });
          }
          sheetnamesForDD(sheetNames);
        } else {
          for (let i = 0; i < worksheets.items.length; i++) {
            sheetNames.push(worksheets.items[i].name);
          }
          return sheetNames;
        }
      })
      .catch(function (error) {
        console.log("Error: " + error);
        if (error instanceof OfficeExtension.Error) {
          console.log("Debug info: " + JSON.stringify(error.debugInfo));
        }
      });
  });
};

export const activateGivenSheet = (sheetname) => {
  Excel.run(function (ctx) {
    ctx.workbook.worksheets.getItem(sheetname).activate();
    return ctx.sync();
  }).catch(function (error) {
    console.error("Error: " + error);
  });
};

