// changeActiveSheetName changes the active  sheet name into given two parameters  like "sheetname-23sd"
export const changeActiveSheetName = async (sheetName, additionalValue) => {
  try {
    if (sheetName.length >= 25) sheetName = sheetName.substring(0, 25);
    let providedSheetName = sheetName.replace(/ /g, "").replace(/-/g, "_");
    providedSheetName = providedSheetName + "-" + additionalValue;

    return await Excel.run(async (context) => {
      let sheetNameChek = context.workbook.worksheets.getItemOrNullObject(providedSheetName).load("name", "id");
      await context.sync();
      if (sheetNameChek.name) {
        activateGivenSheet(sheetNameChek.name);
        return sheetNameChek.name;
      } else {
        let sheet = context.workbook.worksheets.getActiveWorksheet();

        sheet.name = providedSheetName;
        await context.sync();
        return providedSheetName;
      }
    });
  } catch (error) {
    console.log(error);
  }
};

export const bomVersionCheckfromSheetName = async (bomName, versionId) => {
  return await Excel.run(async (context) => {
    console.log(bomName,versionId);
    let curSheetName = context.workbook.worksheets.getActiveWorksheet().load("name");
    await context.sync();
    if (bomName.length >= 25) bomName = bomName.substring(0, 25);
    let requiredSheetName = bomName.replace(/-/g, "_");
    requiredSheetName = requiredSheetName + "-" + versionId;
    if (curSheetName.name === requiredSheetName) return true;
    else return false;
  });
};

