// if we provide anu cell address lilke "sheetName!1A1:A25" or "shettName!AZ45" it will convert it into column address like A , AZ and so on
export const getProperColumnAddress = (address) => {
  console.log(address);
  let cellAddress = address.split("!")[1].split(":")[0];
  console.log(cellAddress, address.split("!")[1]);

  for (let i = 0; i < cellAddress.length; i++) {
    if (/\d/.test(cellAddress)) {
      cellAddress = cellAddress.substring(0, cellAddress.length - 1);
      i--;
    } else {
      return cellAddress;
    }
  }
};
// this function converts given string into respected column number like A=1or AA= 26 ;
//  for this we are using the equavtion clmNo=(ascivalueof 1st char-64)*26^stringLength-1+(ascivalueof 2nd char-64)*26^stringLength-2....(ascivalueof last char-64)*26^stringLength-stringLength
export const getExcelColumnNumber = (address) => {
  let columnNumber = 0;
  if (address?.length > 1) {
    for (let i = 0; i < address.length; i++) {
      columnNumber += (address.charCodeAt(i) - 64) * Math.pow(26, address.length - (i + 1));
    }
    console.log(columnNumber);
    return columnNumber;
  } else {
    columnNumber = address.charCodeAt(0);
    return columnNumber - 64;
  }

  // here it returds if its a single character (if address=A then 65-64=1 )
};
//clearAllbackGroundColorEXA1==> will clear all the background color of active sheet except row 1 (heading)
export const clearAllbackGroundColorEXA1 = async () => {
  await Excel.run(async function (context) {
    let dataRange = context.workbook.worksheets.getActiveWorksheet();
    let address = dataRange.getUsedRange().load("address");

    await context.sync();
    dataRange
      .getRange(address?.address.split("!")[0] + "!" + "A2:" + address?.address.split("!")[1]?.split(":")[1])
      .format.fill.clear();
    return context.sync().then(() => {});
  }).catch(function (error) {
    console.error(error);
  });
};
// this fn writes the heading to the first row of given sheeet from the mappinmg data
export const writeHeadingsToExcel = async (mappingData, key, sheetName) => {
  await Excel.run((context) => {
    const sheet = context.workbook.worksheets.getItem(sheetName).getRange(String(mappingData[key]?.address + 1));
    sheet.values = [[mappingData[key]?.name]];
    sheet.format.fill.color = "#FFC467";
    sheet.format.autofitColumns();
    return context.sync();
  });
};

